import { describe, it, expect, jest } from '@jest/globals';
import { PassThrough } from 'node:stream';
import { UnifiedUIRenderer } from '../src/ui/UnifiedUIRenderer.js';

const createRenderer = () => {
  const input = new PassThrough() as unknown as NodeJS.ReadStream;
  (input as any).isTTY = true;
  (input as any).setRawMode = jest.fn();

  const output = new PassThrough() as unknown as NodeJS.WriteStream;
  (output as any).isTTY = true;
  (output as any).columns = 80;
  (output as any).rows = 24;
  (output as any).write = ((chunk: any) => {
    const text = typeof chunk === 'string' ? chunk : chunk?.toString?.() ?? '';
    // Preserve write signature used by readline
    PassThrough.prototype.write.call(output, text);
    return true;
  }) as any;

  const renderer = new UnifiedUIRenderer(output, input);
  return { renderer, input, output };
};

describe('Paste functionality', () => {
  it('should not leak toggle symbols during paste', () => {
    const { renderer, input } = createRenderer();
    try {
      // Simulate a paste with toggle symbols mixed in
      const testPaste = 'Hello©WorldåTest∂Content';
      
      // Access private method directly for testing
      const stripToggleSymbols = (renderer as any).stripToggleSymbols;
      const sanitizePasteContent = (renderer as any).sanitizePasteContent;
      
      // Test stripToggleSymbols directly
      const stripped = stripToggleSymbols(testPaste);
      expect(stripped).not.toContain('©');
      expect(stripped).not.toContain('å');
      expect(stripped).not.toContain('∂');
      expect(stripped).toBe('HelloWorldTestContent');
      
      // Test sanitizePasteContent
      const sanitized = sanitizePasteContent(testPaste);
      expect(sanitized).toBe('HelloWorldTestContent');
      
    } finally {
      renderer.cleanup();
    }
  });

  it('should handle multi-line paste without visual leak', () => {
    const { renderer, input } = createRenderer();
    try {
      // Simulate a multi-line paste
      const multiLinePaste = 'Line 1\nLine 2\nLine 3';
      
      // Trigger raw data handler
      const dataHandler = (input as any).listeners('data')[0];
      expect(dataHandler).toBeDefined();
      
      // Write multi-line content
      dataHandler(Buffer.from(multiLinePaste));
      
      // The emit-level paste buffer should capture it
      const emitPasteBuffer = (renderer as any).emitPasteBuffer;
      expect(emitPasteBuffer).toBe(multiLinePaste);
      
      // Manually trigger the commit to create collapsed paste
      const commitMethod = (renderer as any).commitEmitPasteBuffer;
      commitMethod.call(renderer);
      
      // Should create collapsed paste instead of leaking to screen
      const collapsedPaste = (renderer as any).collapsedPaste;
      expect(collapsedPaste).toBeDefined();
      expect(collapsedPaste.text).toBe(multiLinePaste);
      expect(collapsedPaste.lines).toBe(3);
      
    } finally {
      renderer.cleanup();
    }
  });

  it('should strip escape sequences from paste content', () => {
    const { renderer } = createRenderer();
    try {
      const sanitizeMethod = (renderer as any).sanitizePasteContent;
      
      // Test with various escape sequences
      const testCases = [
        { input: '\x1b[31mRed Text\x1b[0m', expected: 'Red Text' },
        { input: 'Normal\x1b[200~Pasted\x1b[201~Text', expected: 'NormalPastedText' },
        { input: '\x1b[AUp Arrow', expected: 'Up Arrow' },
      ];
      
      testCases.forEach(({ input, expected }) => {
        const result = sanitizeMethod(input);
        expect(result).toBe(expected);
      });
      
    } finally {
      renderer.cleanup();
    }
  });

  it('should suppress render during paste burst to prevent visual leak', () => {
    const { renderer, input } = createRenderer();
    try {
      // Set up spy on render method
      const renderSpy = jest.spyOn(renderer as any, 'renderPromptImmediate');
      
      // Start a paste burst
      const dataHandler = (input as any).listeners('data')[0];
      dataHandler(Buffer.from('Start of paste'));
      
      // Wait a bit for the timer to be set
      jest.runAllTimers();
      
      // The actual render should be suppressed due to paste burst
      // Check that we're in a paste state
      const inEmitPaste = (renderer as any).emitPasteBuffer.length > 0;
      expect(inEmitPaste).toBe(true);
      
      // Render should be suppressed
      renderer.renderPrompt();
      expect(renderSpy).not.toHaveBeenCalled();
      
    } finally {
      renderer.cleanup();
    }
  });
});